<template>
  <div class="tab-cont-item">
    <TopBottom :dataList="partOneData"></TopBottom>

    <slot name="header"></slot>

    <HoverSwitch title="价值" :dataList="hoverSwitchList"></HoverSwitch>

    <TopBottom :dataList="partTwoData"></TopBottom>

    <slot name="bottom"></slot>
  </div>
</template>

<script>
import HoverSwitch from "@/components/hoverSwitch";
import TopBottom from "@/components/style/TopBottom";

export default {
  name: "SchemeTabCon",
  components: {
    HoverSwitch,
    TopBottom
  },
  props: {
    partOneData: {
      type: Array,
      default: () => {
        return [];
      }
    },
    hoverSwitchList: {
      type: Array,
      default: () => {
        return [];
      }
    },
    partTwoData: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },

  data() {
    return {};
  },

  methods: {}
};
</script>

<style lang="scss" scoped>
// .tab-cont-item{
//     padding: 0 50px;
// }
</style>



