<template>
    <!-- 自上而下 条件渲染 -->
    <div class="fall-box">
        <div v-for="(i, iIndex) in dataList" :key="iIndex">
            <div v-if="i.type === 'CONT'">
                <p
                    class="cont"
                    v-for="(k, kIndex) in i.conts"
                    :key="kIndex"
                    v-html="Wrap(k)"
                ></p>
            </div>

            <div
                class="img"
                v-else-if="i.type === 'IMG'"
                :style="{ width: i.imgWidth / remUnit + 'rem' }"
            >
                <img :src="i.imgUrl" />
            </div>

            <p :class="`title ${i.class}`" v-else-if="i.type === 'TITLE'">
                {{ i.title }}
            </p>

            <div :class="`sub-title ${i.class}`" v-else>{{ i.subTtitle }}</div>
        </div>
    </div>
</template>

<script>
import Common from "@/components/Common";
export default {
    name: "TopBottom",

    props: {
        dataList: {
            type: Array,
            default: () => {
                return [];
            },
        },
    },

    data() {
        return {
            remUnit: Common.remUnit,
        };
    },
    methods: {
        Wrap: (origin) => {
            var res = origin.replace(/\\n/gm, "<br>");
            return res;
        },
    },
};
</script>

<style lang="scss" scoped>
.fall-box {
    margin: 100px auto 0;
    // width: $pc_main_size;
    .title {
        font-size: 34px;
        line-height: 30px;
        // margin-bottom: 20px;
        position: relative;
        padding-left: 20px;
        &.superHeight {
            margin-top: 84px;
        }
        &.superMiddle {
            margin-top: 70px;
        }
        &::after {
            content: "";
            position: absolute;
            background: #3a80e5;
            border-radius: 4.5px;
            width: 4px;
            height: 20px;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
        }
    }
    .cont {
        font-size: 24px;
        line-height: 1.6;
         margin-top: 30px;
    }
    .sub-title {
        font-size: 26px;
        color: #2F61AC;;
        letter-spacing: 0;
        margin-top: 30px;
        // line-height: 50px;
        &.small {
            font-size: 18px;
        }
    }
    .img {
        // text-align: center;
        margin: 0 auto;
        // margin-bottom: 50px;
         margin-top: 50px;
        padding-bottom: 20px;
        img {
            width: 100%;
        }
    }
      >>>.dot{
        color:#4889E6;
        // color:red;
    }
}
</style>



