<template>
    <div class="tab">
        <ul>
            <li
                v-for="(i, iIndex) in tabList"
                :key="iIndex"
                class="item"
                :class="{
                    active: isActive === iIndex,
                }"
                @click="openPage(i.path, iIndex)"
            >
                <span>{{ i }}</span>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name: "Tab",
    props: {
        tabList: {
            type: Array,
            default: () => {
                return [];
            },
        },
    },
    data() {
        return {
            isActive: 0, // 选中
        };
    },

    created() {},

    methods: {
        openPage(path, index) {
            this.isActive = index;
            this.$emit("changTab", index);
        },
        changeActive(index) {
            this.isActive = index;
        },
    },
};
</script>

<style lang="scss" scoped>
.tab {
    width: 100%;
    overflow: auto;
    height: 80px;
    border-bottom: 1px solid #e5eaf1;
    padding:0 50px;
    box-sizing: border-box;
    ul {
        height: 100%;
        white-space: nowrap;
    }
    li {
        display: inline-block;
        vertical-align: top;
        font-size: 28px;
        height: 100%;
        line-height: 80px;
        color: #666666;
        margin-right: 40px;
        position: relative;
        transition: color 0.3s;
        &::after {
            content: "";
            height: 4px;
            position: absolute;
            background: #2D93FF;
            border-radius: 3px;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 0%;
            // transition: width 0.3s;
        }

        &.active {
            color: #2d93ff;
            position: relative;
            // font-size: 30px;
            font-weight: 400;
            &::after {
                width: 100%;
            }
        }
    }
}
</style>



